@import url(https://fonts.googleapis.com/css?family=Nunito&display=swap);
body {
  margin: 0; }

.form {
  display: flex;
  align-items: center;
  flex-direction: column;
  border: 1px solid #40e1d1;
  border-radius: 6px;
  padding: 50px 30px; }
  .form-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90vh; }
  .form__title {
    font-family: "Nunito", sans-serif;
    margin: 30px 0; }
  .form__img {
    width: 80px;
    height: 60px; }
  .form__submit {
    font-family: "Nunito", sans-serif;
    background-color: cadetblue;
    width: 120px;
    height: 40px;
    border: none;
    font-weight: 700;
    margin-top: 20px; }
  .form__submit:hover {
    border: 1px solid cadetblue;
    background-color: white;
    cursor: pointer; }

.input-wrapper {
  padding: 20px;
  position: relative;
  width: 350px; }
  .input-wrapper__label {
    font-family: "Nunito", sans-serif;
    font-size: 14px;
    font-weight: 500;
    position: absolute;
    top: 0px; }
  .input-wrapper__input {
    font-family: "Nunito", sans-serif;
    font-size: 14px;
    border-width: 0 0 1px 0;
    border-color: #40e1d1;
    padding: 10px 0;
    width: 100%; }
  .input-wrapper__error {
    font-family: "Nunito", sans-serif;
    color: brown;
    font-size: 14px; }

.filter-wrapper {
  display: flex;
  justify-content: space-between;
  padding: 20px; }
  @media (max-width: 960px) {
    .filter-wrapper {
      flex-direction: column; } }

.search {
  display: flex; }
  .search__input {
    border: 1px solid #8aff !important;
    color: #8aff;
    font-family: "Nunito", sans-serif;
    width: 400px;
    height: 42px;
    font-size: 16px;
    padding: 0 10px; }
    @media (max-width: 960px) {
      .search__input {
        width: 100%;
        margin-bottom: 15px; } }
  .search__input::-webkit-input-placeholder {
    opacity: 1; }
  .search__input::-moz-placeholder {
    opacity: 1; }
  .search__input:-ms-input-placeholder {
    opacity: 1; }
  .search__input::-ms-input-placeholder {
    opacity: 1; }
  .search__input::placeholder {
    opacity: 1; }

.select {
  display: flex; }
  .select-wrapper {
    margin: 0 20px;
    width: 200px; }
    @media (max-width: 960px) {
      .select-wrapper {
        margin: 0;
        padding-right: 12px; } }
  .select__item {
    border: 1px solid #8aff !important;
    color: #8aff;
    font-family: "Nunito", sans-serif; }
    .select__item input::-webkit-input-placeholder {
      color: #8aff;
      font-family: "Nunito", sans-serif;
      opacity: 1; }
    .select__item input::-moz-placeholder {
      color: #8aff;
      font-family: "Nunito", sans-serif;
      opacity: 1; }
    .select__item input:-ms-input-placeholder {
      color: #8aff;
      font-family: "Nunito", sans-serif;
      opacity: 1; }
    .select__item input::-ms-input-placeholder {
      color: #8aff;
      font-family: "Nunito", sans-serif;
      opacity: 1; }
    .select__item input::placeholder {
      color: #8aff;
      font-family: "Nunito", sans-serif;
      opacity: 1; }
  @media (max-width: 960px) {
    .select {
      justify-content: space-between; } }

.news-block {
  width: 20%;
  padding: 15px;
  border: 1px solid #8aff;
  margin: 20px 0;
  background-color: #e1f1ff; }
  .news-block__title {
    color: #8aff;
    font-family: "Nunito", sans-serif;
    font-size: 18px; }
  .news-block__img {
    max-width: 100%;
    max-height: 300px; }
  .news-block__description {
    max-width: 100%;
    font-family: "Nunito", sans-serif;
    font-size: 14px;
    margin-top: 15px; }
  @media (max-width: 1280px) {
    .news-block {
      width: 28%; } }
  @media (max-width: 960px) {
    .news-block {
      width: 40%; } }
  @media (max-width: 768px) {
    .news-block {
      width: 85%; } }

.pagination {
  display: flex;
  justify-content: center; }
  .pagination__arrow {
    font-size: 45px;
    color: #8af;
    font-weight: bold;
    padding-bottom: 15px; }
  .pagination__arrow:hover {
    cursor: pointer;
    color: #40e1d1; }

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #042a3f;
  padding: 25px;
  background-color: #e1f1ff; }
  .menu__img {
    width: 80px;
    height: 60px; }
  .menu__button-logout {
    font-size: 16px;
    font-weight: bold;
    height: 18px;
    color: #ed4455;
    font-family: "Nunito", sans-serif;
    font-size: 18px; }
    .menu__button-logout:hover {
      cursor: pointer; }

.nav {
  display: flex;
  justify-content: space-between;
  width: 40%; }
  .nav__link {
    list-style-type: none;
    font-size: 16px;
    font-weight: bold;
    border-bottom: 2px solid #042a3f; }
    .nav__link a {
      color: black;
      text-decoration: none;
      font-family: "Nunito", sans-serif;
      font-size: 18px; }

.footer {
  background-color: #042a3f;
  padding: 25px 0; }
  .footer-links {
    list-style-type: none;
    font-weight: bold; }
    .footer-links a {
      color: white;
      text-decoration: none;
      font-family: "Nunito", sans-serif;
      font-size: 18px; }


.news {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap; }


div .testDiv {
  color: red; }

