.form {
  display: flex;
  align-items: center;
  flex-direction: column;
  border: 1px solid #40e1d1;
  border-radius: 6px;
  padding: 50px 30px;

  &-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 90vh;
  }

  &__title {
    font-family: "Nunito", sans-serif;
    margin: 30px 0;
  }

  &__img {
    width: 80px;
    height: 60px;
  }

  &__submit {
    font-family: "Nunito", sans-serif;
    background-color: cadetblue;
    width: 120px;
    height: 40px;
    border: none;
    font-weight: 700;
    margin-top: 20px;
  }

  &__submit:hover {
    border: 1px solid cadetblue;
    background-color: white;
    cursor: pointer;
  }
}
